import { fabric } from 'fabric';
import {
  changeDirection,
  getDistanceBetweenTwoPoints,
  getDistanceFromPointToLine,
} from './geometry';

/**
 * For drawing, you have to update the lines array
 * But after updating, it doesn't affect to the result
 * So, you have to make a new array to affect
 * @param   { fabric.Line[] }  lines // original array
 * @param   { string }         color // the color of line
 * @returns { fabric.Line[] }        // new array
 */
export const makeNewLines = (
  lines: fabric.Line[],
  color: string
): fabric.Line[] => {
  let newLines: fabric.Line[] = [];

  lines.forEach((value) => {
    const newLine = new fabric.Line(
      [value.x1!, value.y1!, value.x2!, value.y2!],
      {
        strokeWidth: 3,
        stroke: color,
        selectable: false,
      }
    );
    newLines.push(newLine);
  });

  return newLines;
};

/**
 * Find close line which two ends of each line are close
 * This is used for joining lines within tolerance
 * @param     { fabric.Line[] }   lines           // line array
 * @param     { fabric.Line }     line            // current Line
 * @param     { number }          index           // index of current line
 * @param     { number }          joinTolerance   // index of current line
 * @returns   { fabric.Line }                     // next close line
 */
export const findTargetLine = (
  lines: fabric.Line[],
  line: fabric.Line,
  index: number,
  joinTolerance: number
): any => {
  const targetLine = lines.find(
    (l, j) =>
      j >= index &&
      ((getDistanceBetweenTwoPoints(
        { x: l.x1!, y: l.y1! },
        { x: line.x1!, y: line.y1! }
      ) < joinTolerance &&
        !(l.x1 === line.x1 && l.y1 === line.y1)) ||
        (getDistanceBetweenTwoPoints(
          { x: l.x2!, y: l.y2! },
          { x: line.x1!, y: line.y1! }
        ) < joinTolerance &&
          !(l.x2 === line.x1 && l.y2 === line.y1)) ||
        (getDistanceBetweenTwoPoints(
          { x: l.x1!, y: l.y1! },
          { x: line.x2!, y: line.y2! }
        ) < joinTolerance &&
          !(l.x1 === line.x2 && l.y1 === line.y2)) ||
        (getDistanceBetweenTwoPoints(
          { x: l.x2!, y: l.y2! },
          { x: line.x2!, y: line.y2! }
        ) < joinTolerance &&
          !(l.x2 === line.x2 && l.y2 === line.y2)))
  );

  return targetLine;
};

/**
 * Get connect lines of current line
 * This is used for removing nodes within tolerance
 * @param { fabric.Line[] }   lines             // line array
 * @param { fabric.Line[] }   connectLines      // connect line array of current line
 * @param { number[] }        flag              // visited lines
 */
export const getConnectedLines = (
  lines: fabric.Line[],
  connectLines: fabric.Line[],
  flag: number[]
) => {
  let going = true;
  while (going) {
    let lastLine = connectLines[connectLines.length - 1];
    let targetLine = lines.find(
      (l, index) =>
        flag[index] === 0 &&
        ((lastLine.x2 === l.x1 && lastLine.y2 === l.y1) ||
          (lastLine.x2 === l.x2 && lastLine.y2 === l.y2))
    );
    if (targetLine) {
      if (lastLine.x2 === targetLine.x2) changeDirection(targetLine);
      connectLines.push(targetLine);
      flag[lines.indexOf(targetLine)] = 1;
    } else {
      going = false;
    }
  }
  going = true;
  while (going) {
    let lastLine = connectLines[0];
    let targetLine = lines.find(
      (l, index) =>
        flag[index] === 0 &&
        ((lastLine.x1 === l.x1 && lastLine.y1 === l.y1) ||
          (lastLine.x1 === l.x2 && lastLine.y1 === l.y2))
    );
    if (targetLine) {
      if (lastLine.x1 === targetLine.x1) changeDirection(targetLine);
      connectLines.unshift(targetLine);
      flag[lines.indexOf(targetLine)] = 1;
    } else {
      going = false;
    }
  }
};

/**
 * Remove nodes within tolerance
 * @param     { fabric.Line[] }   connectedLines   // connected Line array
 * @param     { number }          removeTolerance  // remove tolerance
 * @returns   { fabric.Line[] }                    // updated connected array
 */
export const removeNodesCore = (
  connectedLines: fabric.Line[],
  removeTolerance: number
): fabric.Line[] => {
  let resultLines = [connectedLines[0]];
  connectedLines.forEach((_, index) => {
    if (index > 0) {
      const count = resultLines.length;
      const newLine = new fabric.Line([
        resultLines[count - 1].x1!,
        resultLines[count - 1].y1!,
        connectedLines[index].x2!,
        connectedLines[index].y2!,
      ]);

      const distance = getDistanceFromPointToLine(
        { x: connectedLines[index].x1!, y: connectedLines[index].y1! },
        newLine
      );

      if (distance <= removeTolerance) {
        resultLines.pop();
        resultLines.push(newLine);
      } else {
        resultLines.push(connectedLines[index]);
      }
    }
  });
  return resultLines;
};
