import { fabric } from 'fabric';
import { getDistanceBetweenTwoPoints } from './geometry';
import {
  findTargetLine,
  getConnectedLines,
  makeNewLines,
  removeNodesCore,
} from './assist';

/**
 * Join Lines from origin array
 * @param     { fabric.Line[] }   lineArr         // original array
 * @param     { number }          joinTolerance   // join tolerance
 * @returns   { fabric.Line[] }                   // joined array
 */
export const joinLines = (
  lineArr: fabric.Line[],
  joinTolerance: number
): fabric.Line[] => {
  let lines = lineArr;
  joinLinesCore(lines, joinTolerance);
  return makeNewLines(lines, 'red');
};

/**
 * Join lines within tolerance
 * @param   { fabric.Line[] }   lines           // line array
 * @param   { number }          joinTolerance   // original array
 */
const joinLinesCore = (lines: fabric.Line[], joinTolerance: number) => {
  let hasJoiningLine = false;
  for (let i = 0; i < lines.length; i++) {
    const line = lines[i];
    const targetLine = findTargetLine(lines, line, i, joinTolerance);
    if (targetLine) {
      hasJoiningLine = true;
      if (
        getDistanceBetweenTwoPoints(
          { x: targetLine.x1!, y: targetLine.y1! },
          { x: line.x1!, y: line.y1! }
        ) < joinTolerance
      ) {
        targetLine.x1 = line.x1;
        targetLine.y1 = line.y1;
      } else if (
        getDistanceBetweenTwoPoints(
          { x: targetLine.x1!, y: targetLine.y1! },
          { x: line.x2!, y: line.y2! }
        ) < joinTolerance
      ) {
        targetLine.x1 = line.x2;
        targetLine.y1 = line.y2;
      } else if (
        getDistanceBetweenTwoPoints(
          { x: targetLine.x2!, y: targetLine.y2! },
          { x: line.x1!, y: line.y1! }
        ) < joinTolerance
      ) {
        targetLine.x2 = line.x1;
        targetLine.y2 = line.y1;
      } else {
        targetLine.x2 = line.x2;
        targetLine.y2 = line.y2;
      }

      break;
    }
  }

  if (hasJoiningLine) {
    joinLines(lines, joinTolerance);
  }
};

/**
 * Remove nodes within tolerance
 * @param     { fabric.Line[] }  lineArr          // original line array
 * @param     { number }         removeTolerance  // remove tolearnce
 * @returns   { fabric.Line[] }                   // removed line array
 */
export const removeNodes = (
  lineArr: fabric.Line[],
  removeTolerance: number
): fabric.Line[] => {
  let lines = lineArr,
    changedLines: fabric.Line[] = [],
    flag: number[] = [];
  flag = Array(lines.length).fill(0);
  lines.forEach((_, index) => {
    if (!flag[index]) {
      let connectLines = [lines[index]];
      flag[index] = 1;
      getConnectedLines(lines, connectLines, flag);
      if (connectLines.length === 1) {
        changedLines.push(connectLines[0]);
      } else {
        let removedLines = removeNodesCore(connectLines, removeTolerance);
        removedLines.forEach((value: fabric.Line) => changedLines.push(value));
      }
    }
  });
  return makeNewLines(changedLines, 'blue');
};
