import React, { useEffect, useRef, useState } from 'react';
import { fabric } from 'fabric';

import { joinLines, removeNodes } from '../utils/operation';
import { getDistanceBetweenTwoPoints } from '../utils/geometry';
import { CANVAS_WIDTH, CANVAS_HEIGHT, JOIN_TOLERNACE, REMOVE_TOLERENACE } from '../constants'

const buttonStyle = {
  margin: '10px 20px',
  padding: '20px',
  backgroundColor: 'lavenderblush',
  fontSize: 20,
  cursor: 'pointer',
  borderRadius: 20,
  border: '1px solid powderblue'
}

const inputStyle = {
  margin: '10px',
  lineHeight: '20px',
  fontSize: 20
}

const Canvas: React.FC = () => {
  const canvasRef = useRef<fabric.Canvas | null>(null);
  const curLine = useRef<fabric.Line | null>(null)
  const [lines, setLines] = useState<fabric.Line[] | null>([])
  const [joinedLines, setJoinedLines] = useState<fabric.Line[] | null>([])
  const [removedLines, setRemovedLines] = useState<fabric.Line[] | null>([])
  const [joinFinished, setJoinFinished] = useState(true)
  const [removeFinished, setRemoveFinished] = useState(true)
  const [joinTolerance, setJoinTolerance] = useState<number>(JOIN_TOLERNACE)
  const [removeTolerance, setRemoveTolerance] = useState(REMOVE_TOLERENACE)
  const [currentStatus, setCurrentStatus] = useState('Please draw multiple lines')
  const drawingEnabled = useRef(true)
  const isDrawing = useRef(false);

  useEffect(() => {
    canvasRef.current = new fabric.Canvas('myCanvas', {
      backgroundColor: '#ECDFA5',
      height: CANVAS_HEIGHT,
      width: CANVAS_WIDTH,
      selection:false,
    });

    canvasRef.current.on('mouse:down', (event) => handleMouseDown(event, canvasRef.current!))
    canvasRef.current.on('mouse:move', (event) => handleMouseMove(event, canvasRef.current!))
    canvasRef.current.on('mouse:up', (event) => handleMouseUp(event, canvasRef.current!))

    return () => {
      canvasRef.current!.off('mouse:down')
      canvasRef.current!.off('mouse:move')
      canvasRef.current!.off('mouse:up')
      canvasRef.current!.dispose();
    }
  }, []);

  useEffect(() => {
    joinedLines?.forEach((value: fabric.Line) => {
      canvasRef.current!.add(value);
    })
    canvasRef.current!.renderAll()
  }, [joinedLines])

  useEffect(() => {
    removedLines?.forEach((value: fabric.Line) => {
      canvasRef.current!.add(value);
    })
    canvasRef.current!.renderAll()
  }, [removedLines])

  const handleMouseDown = (event: fabric.IEvent, canvas: fabric.Canvas) => {
    if (!drawingEnabled)  return;
    isDrawing.current = true;
    const pointer = canvas.getPointer(event.e)
    const line = new fabric.Line(
      [pointer.x, pointer.y, pointer.x, pointer.y], {
        strokeWidth: 3,
        stroke: 'black',
        selectable: false,
    })
    curLine.current = line
    canvas.add(curLine.current)
  };

  const handleMouseMove = (event: fabric.IEvent, canvas: fabric.Canvas) => {
    if (!isDrawing.current || !drawingEnabled.current) return
    const pointer = canvas.getPointer(event.e)
    const line = curLine.current
    line!.set({
      x2: pointer.x,
      y2: pointer.y
    })
    curLine.current = line;
    canvas.renderAll()
  };

  const handleMouseUp = (event: fabric.IEvent, canvas: fabric.Canvas) => {
    if (!drawingEnabled.current)  return;
    isDrawing.current = false
    if (getDistanceBetweenTwoPoints(
      { x: curLine.current!.x1!, y: curLine.current!.y1! },
      { x: curLine.current!.x2!, y: curLine.current!.y2! },
    ) > joinTolerance) {
      setLines((prevLines) => [...prevLines!, curLine.current!])
      setCurrentStatus('Please press JOIN button')
      setJoinFinished(false)
    } else {
      canvas.remove(curLine.current!)
    }
  };

  const joinLinesWithinTolerance = () => {
    if (!drawingEnabled.current)  return;
    let changedLines: fabric.Line[] = [];
    lines?.forEach((value: fabric.Line) => {
      changedLines.push(value);
    })
    changedLines = joinLines(changedLines, joinTolerance);
    setJoinedLines(changedLines);
    setJoinFinished(true)
    setRemoveFinished(false)
    setCurrentStatus('Please press REMOVE button')
    drawingEnabled.current = false;
  };

  const removeNodesWithinTolerance = () => {
    let changedLines: fabric.Line[] = [];
    joinedLines?.forEach((value: fabric.Line) => {
      changedLines.push(value);
    })
    changedLines = removeNodes(changedLines, removeTolerance);
    setRemovedLines(changedLines)
    setRemoveFinished(true)
    setCurrentStatus('Please press CLEAR button and restart')
  }

  const clearCanvas = () => {
    canvasRef.current!.clear()
    canvasRef.current!.setBackgroundColor('#ECDFA5', () => {})
    setLines([])
    setJoinedLines([])
    setRemovedLines([])
    drawingEnabled.current = true;
    setJoinFinished(false)
    setRemoveFinished(false)
    setCurrentStatus('Please draw multiple lines')
  }

  return (
    <div style={{
      fontSize: 20
    }}>
      <h1 style={{color: 'blue'}}>{currentStatus}</h1>
      <label>Join Tolerance: </label>
      <input type="number" defaultValue={JOIN_TOLERNACE} onChange={(e) => setJoinTolerance(Number(e.target.value))} style={inputStyle}/>
      <label>Remove Tolerance: </label>
      <input type="number" defaultValue={REMOVE_TOLERENACE} onChange={(e) => setRemoveTolerance(Number(e.target.value))} style={inputStyle}/>
      <div style={{
        display: 'flex',
        margin: '30px auto',
        width: CANVAS_WIDTH + 200,
      }}>
        <div style={{
          display: 'flex',
          gap: 24,
          flexDirection: 'column',
          width: 200
        }}>
          <button onClick={joinLinesWithinTolerance} style={buttonStyle} disabled={joinFinished}>
            Join Lines
          </button>
          <button onClick={removeNodesWithinTolerance} style={buttonStyle} disabled={removeFinished}>
            Remove Nodes
          </button>
          <button onClick={clearCanvas} style={buttonStyle}>
            Clear
          </button>
        </div>
        <canvas id="myCanvas" style={{cursor: 'crosshair'}}/>
      </div>
    </div>
  );
};

export default Canvas;
