/**
 * Change the direction of given line
 * @param { fabric.Line }   line   // given line
 */
export const changeDirection = (line: fabric.Line) => {
  let tempX = line.x1;
  let tempY = line.y1;

  line.set({
    x1: line.x2,
    y1: line.y2,
    x2: tempX,
    y2: tempY,
  });
};

/**
 * Get distance from a specific point to a line
 * @param     { Point }   point   // Point A
 * @param     { Line }    line    // Line
 * @returns   { number }          // The Distance
 */
export const getDistanceFromPointToLine = (
  point: { x: number; y: number },
  line: fabric.Line
): number => {
  const slope = (line.y2! - line.y1!) / (line.x2! - line.x1!);
  const yIntercept = line.y1! - slope * line.x1!;

  const distance =
    Math.abs(slope * point.x - point.y + yIntercept) /
    Math.sqrt(slope * slope + 1);

  return distance;
};

/**
 * Get distance between two points
 * @param     { x, y } ptA      // Point A
 * @param     { x, y } ptB      // Point B
 * @returns   { number }        // The Distance
 */

export const getDistanceBetweenTwoPoints = (
  ptA: { x: number; y: number },
  ptB: { x: number; y: number }
): number => {
  const subX = ptB.x - ptA.x;
  const subY = ptB.y - ptA.y;
  const distance = Math.sqrt(subX * subX + subY * subY);

  return distance;
};
